<template>
  <div class="bottom-bar">
    <div class="check-content">
      <check-button class="bottom" @click.native="btnclick" :isActive="ischeck"></check-button>
      <span>全选</span>
      <div class="price">合计:{{totalPrice}}</div>
      <div class="calculate" @click="calcuclick">
        <span>去计算({{calculate}})</span>
      </div>
    </div>
    <div>
      <toast :message="message" :isShow="Show" />
    </div>
  </div>
</template>

<script>
import CheckButton from "./CheckButton";
import Toast from "components/common/toast/Toast";
export default {
  name: "CartBottomBar.",
  props: {},
  data() {
    return {
      message: "",
      Show: false,
      test1:null,
    };
  },
  components: {
    CheckButton,
    Toast,
  },
  mounted() {},
  methods: {
    btnclick() {
      if (this.ischeck) {
        //全部选中
        this.$store.state.cartList.map((item) => (item.isActive = false));
      } else {
        //部分或全部不选中
        this.$store.state.cartList.map((item) => (item.isActive = true));
      }
    },
    calcuclick() {
      //判断全选或者部分选中
      if (!this.ischeck && this.calculate==0) {

        // 清除定时器
        clearTimeout(this.test1)

        // 显示toast信息
        this.message = "你没有选择商品";
        this.Show = true;
        
        this.test1 = setTimeout(() => {
          //关闭toast信息
          this.Show = false;
        }, 1500);
        //全部没选中
      } else {
        clearTimeout(this.test1);
        this.message = "此功能待完善";
        this.Show = true;
        this.test1 = setTimeout(() => {
          this.Show = false;
        }, 1500);
      }
    },
  },
  computed: {
    totalPrice() {
      return (
        "￥" +
        this.$store.state.cartList
          .filter((item) => {
            return item.isActive;
          })
          .reduce((preValue, item) => {
            return preValue + item.lowNowPrice * item.count;
          }, 0)
          .toFixed(2)
      ); //保留俩位小数
    },
    calculate() {
      return this.$store.state.cartList.filter((item) => item.isActive).length;
    },
    ischeck() {
      //判断是否有商品
      if (this.$store.state.cartList.length) {
        //查找商品长度是否为ture
        return !this.$store.state.cartList.find((item) => !item.isActive);
      }
    },
  },
};
</script>

<style scoped>
.bottom-bar {
  height: 40px;
  background-color: #eee;
  line-height: 40px;
  position: relative;
  font-size: 14px;
}
.check-content {
  display: flex;
  align-items: center;
  margin-left: 5px;
}
.bottom {
  height: 20px;
  width: 20px;
  line-height: 20px;
  margin-right: 5px;
}
.price {
  margin-left: 10%;
}
.calculate {
  background-color: red;
  margin-left: auto;
  width: 90px;
  text-align: center;
  color: #fff;
}
</style>