<template>
  <div class="cartlist">
    <better-scroll class="content">
     <cart-list-item v-for="(item,index) in cartlist" 
     :itemInfo='item' 
     :key="index"/>
    </better-scroll>
  </div>
</template>

<script>
import BetterScroll from "components/common/scroll/BetterScroll";
import CartListItem from './CartListItem';
export default {
  name: "CartList",
  data() {
    return {};
  },
  components: {
    BetterScroll,
    CartListItem
  },
  mounted() {},
  methods: {},
  computed: {
    cartlist() {
      return this.$store.state.cartList;
    },
  },
};
</script>

<style scoped>
.cartlist {
  height: calc(100% - 133px );
}
.content {
  height: 100%;
  overflow: hidden;
}
</style>